import { DependencyList } from 'react';

/* eslint-disable indent */
export function isMobile() {
    const browserName = navigator.userAgent;

    const isPad = /ipad/i.test(browserName);  // 
    const isIphone = /iphone/i.test(browserName); // 
    const isMoto = /moto/i.test(browserName); // 
    const isSamsung = /sm/i.test(browserName); // 
    const isUcWeb = /ucweb/i.test(browserName);  // UC
    const isAndroid = /Android/i.test(browserName); // Android 

    return isPad || isIphone || isMoto || isSamsung || isUcWeb || isAndroid;
}

/**
 * 
 * @param oldDeps 
 * @param deps 
 * @returns 
 */
export function diffDependencys(oldDeps: DependencyList, deps: DependencyList) {
    if (oldDeps === deps) return true;
    for (let i = 0; i < oldDeps.length; i++) {
        if (!Object.is(oldDeps[i], deps[i])) return false;
    }
    return true;
}